  body{
    background-color: grey;
  }
  .App {
    text-align: center;
  }
  .App-header {
    /* background-color: #282c34; */
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: black;
  }

  .judul{
    position: absolute;
    left: 150px;
    top: 10px;
  }
  
  .App-link {
    color: #61dafb;
  }
  
  .Movie-container{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    width: 100%;
    gap: 20px;
  }
  
  
  .Movie-wrapper{
    margin-top: 20px;
    width: 400px;
    background-color: rgb(12, 12, 12);
    color: whitesmoke;
  }

  .Movie-image{
    width: 50%;
    margin-top: 10px;
  }
  
  .Movie-search{
    height: 4px;
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 32px;
    padding: 16px;
    width: 50%;
    margin-top:70px;
  }
  .Movie-date{
    font-style: italic;
    margin-top: 24px;
  }
  
  .Movie-rate{
    color: white;
    margin-bottom: 24px;
    font-weight: bold;
    font-size: 1.2em;
    text-decoration: underline;
  }
  
  .Movie-tittle{
    height: 60px;
    margin-top: 20px;
    margin-bottom: 20px;
    font-weight: bold;
  }

@media(max-width:567px){
  .judul{
    left: 15px;
  }
  .Movie-wrapper{
    width: 300px;
  }
}


